import React, { Component } from 'react';
import pets from '../../sevices/pets.json';
import style from './Pets.module.css';


const getIdFromProps = props => props.match.params.id;
class PetsAbout extends Component {
    state = {
        items: [],
    }

    componentDidMount() {
        const idItem = getIdFromProps(this.props);
        let items = pets.filter(item => item.id === idItem);
        this.setState({ items })
    }

    goToPets = () => {
        this.props.history.push('/pets');
    }
    render() {
        const { items } = this.state;
        console.log('items', items);
        return (
            <>

                <button className={style.button} onClick={this.goToPets}>Go To Pets</button>

                {items.map(item => (

                    <div key={item.id} className={style.describe}>
                        <img className={style.img} src={item.image} height='200' width='400' alt={item.breed} />
                        <h3>Name: {item.name} </h3>
                        <span>gender: {item.gender}</span>
                        <p>Color: {item.color}</p>
                        <p className={style.p}>{item.description}</p>
                    </div>
                ))}
            </>
        )
    }
}

export default PetsAbout;